body {
  color: #fff;
  text-align: center;
  background-color: #0a0a0a;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  background-color: #1a1a1a;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
}

.logo-container {
  background: #ffffff1a;
  border-radius: 8px;
  align-items: center;
  gap: 10px;
  padding: 10px;
  display: flex;
}

.app-logo {
  object-fit: contain;
  width: 40px;
  height: 40px;
}

.app-title {
  color: #fff;
  font-size: 18px;
  font-weight: bold;
}

nav ul {
  gap: 20px;
  list-style: none;
  display: flex;
}

nav ul li {
  display: inline-block;
}

nav ul li a {
  color: #fff;
  font-weight: bold;
  text-decoration: none;
}

.budget-summary {
  padding: 50px 20px;
}

.summary-boxes {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.summary-item {
  text-align: center;
  background: #1a1a1a;
  border-radius: 5px;
  width: 200px;
  padding: 20px;
}

.input-section {
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 50px;
  padding: 50px 20px;
  display: flex;
}

#input-fields-container {
  background: #1a1a1a;
  border-radius: 10px;
  width: 400px;
  padding: 30px;
  box-shadow: 0 4px 10px #8a2be266;
}

form {
  flex-direction: column;
  align-items: center;
  display: flex;
}

label {
  margin-top: 10px;
}

input, select {
  border: none;
  border-radius: 5px;
  width: 80%;
  max-width: 300px;
  margin: 5px 0;
  padding: 10px;
}

.btn {
  color: #fff;
  background-color: #8a2be2;
  border: none;
  border-radius: 5px;
  margin-right: 2px;
  padding: 10px 15px;
}

.cta-btn {
  color: #fff;
  cursor: pointer;
  background-color: #8a2be2;
  border-radius: 5px;
  margin-top: 20px;
  padding: 15px 25px;
  font-weight: bold;
  text-decoration: none;
  display: inline-block;
}

.expense-list {
  padding: 50px 20px;
}

#expense-history {
  max-width: 500px;
  margin: 0 auto;
  padding: 0;
  list-style: none;
}

#expense-history li {
  background: #1a1a1a;
  border-radius: 5px;
  justify-content: space-between;
  margin: 10px 0;
  padding: 10px;
  display: flex;
}

#chatbot-container {
  color: #fff;
  background: #1a1a1a;
  border: 2px solid #8a2be2;
  border-radius: 10px;
  width: 320px;
  padding: 15px;
  font-family: Arial, sans-serif;
  box-shadow: 0 4px 10px #8a2be266;
}

#chat-history {
  background: #0a0a0a;
  border: 1px solid #8a2be2;
  border-radius: 8px;
  height: 250px;
  max-height: 250px;
  margin-bottom: 10px;
  padding: 10px;
  overflow-y: auto;
}

#chat-input {
  color: #fff;
  background-color: #0a0a0a;
  border: 1px solid #8a2be2;
  border-radius: 5px;
  outline: none;
  width: 75%;
  padding: 10px;
  font-size: 14px;
}

#send-btn {
  color: #fff;
  cursor: pointer;
  background-color: #8a2be2;
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
  font-weight: bold;
}

#send-btn:hover {
  background-color: #6a1bb5;
}

.history {
  word-wrap: break-word;
  border-radius: 8px;
  max-width: 85%;
  margin: 5px 0;
  padding: 8px 12px;
}

.history.user {
  color: #fff;
  text-align: right;
  background: #8a2be2;
  align-self: flex-end;
}

.history.bot {
  color: #ddd;
  text-align: left;
  background: #2a2a2a;
  align-self: flex-start;
}

@media (width <= 1024px) {
  .input-section {
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }

  #chatbot-container {
    width: 90%;
    margin-top: 20px;
    position: relative;
    right: auto;
    transform: none;
  }

  .summary-boxes {
    flex-direction: column;
    align-items: center;
  }
}

@media (width <= 768px) {
  header {
    text-align: center;
    flex-direction: column;
  }

  nav ul {
    flex-direction: column;
    gap: 10px;
  }

  .summary-boxes, .input-section {
    flex-direction: column;
    align-items: center;
  }

  #input-fields-container {
    width: 90%;
  }

  #chatbot-container {
    width: 100%;
  }
}
